import React from 'react';
import { connect } from 'react-redux';
import Header from '../../components/Header/Header';
import '../../styles/global.css';
import './About.css';

/* Couleurs de l'appli et thmes */
const colors = {
	light: {
		mainBgColor: '#F0F0F0',
		// textColor: 'red' // TBD
	},
	dark: {
		mainBgColor: '#181818',
		// textColor: '#d3d3d3' // TBD
	}
}

const getClassName = (baseClassName, theme) => {
	let res = baseClassName;
	res += ` ${baseClassName}-${theme}`;
	return res;
}

const About = (props) => {
	const theme = props.activeTheme;

	return (
		<div
			style={{backgroundColor: colors[theme].mainBgColor}}
			className="page-main-div"
		>
			<Header />
			<div className="page-content-div">
				<section className={getClassName('about-section', theme)}>
					<span className="about-section-title">technology stack</span>
					<ul>
						<li>React JS, <span>frontend framework</span></li>
						<li>Redux, <span>state management framework</span></li>
						<li>Font Awesome, <span>icons library</span></li>
						<li>unDraw, <span>open source illustrations library</span></li>
					</ul>
				</section>
				<section className={getClassName('about-section', theme)}>
					<span className="about-section-title">developer info</span>
					<ul>
						<li className='link-item'><a href="https://github.com/lukaasblu" target="_blank" rel="noopener noreferrer">Github Account: <span>@lukaasblu</span></a></li>
						<li className='link-item'><a href="https://www.freecodecamp.org/lukaasblu" target="_blank" rel="noopener noreferrer">freeCodeCamp Account: <span>@lukaasblu</span></a></li>
						<li className='link-item'><a href="mailto:l.blu.pro@gmail.com" target="_blank" rel="noopener noreferrer">Email Address: <span>l.blu.pro@gmail.com</span></a></li>
					</ul>
				</section>
			</div>
		</div>
	);
}

const mapStateToProps = (state) => {
	return {
		activeTheme: state.theme.activeTheme
	}
}

export default connect(mapStateToProps)(About);
