import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import '../../styles/global.css';
import './Todos.css';
import Header from '../../components/Header/Header';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPen, faTrash, faCheck, faRotateLeft } from '@fortawesome/free-solid-svg-icons';
import isValidText from '../../helpers/isValidText';
import NoTodosImageDark from '../../assets/images/undraw_no_data_dark.svg';
import NoTodosImageLight from '../../assets/images/undraw_no_data_light.svg';

import { ToastContainer, toast } from 'react-toastify';

const noTodosText = 'Wow, such empty !';
const addTodoBtn = {
	text: 'ADD',
	backgroundColor: '#4285F4'
};
const editTodoBtn = {
	text: 'UPDATE',
	backgroundColor: '#F4C20D'
};

const mockTodos = [
	{ id: '1', text: 'Buy groceries for tonight\'s dinner', completed: true },
	{ id: '2', text: 'Complete the Todos React JS App', completed: false },
	{ id: '3', text: 'Study GraphQL', completed: true }
];

const getClassName = (baseClassName, theme) => {
	let res = baseClassName;
	res += ` ${baseClassName}-${theme}`;
	return res;
}

/* Couleurs de l'appli et thmes */
const colors = {
	light: {
		mainBgColor: '#F0F0F0',
		// textColor: 'red' // TBD
	},
	dark: {
		mainBgColor: '#181818',
		// textColor: '#d3d3d3' // TBD
	}
}

/* Component Todos */
const Todos = (props) => {
	const [todos, setTodos] = useState([...mockTodos]);
	const [todoInputValue, setTodoInputValue] = useState('');
	const [addAndEditBtn, setAddAndEditBtn] = useState(addTodoBtn);
	const [editTodoId, setEditTodoId] = useState(null);

	const theme = props.activeTheme;

	const editTodo = (item) => {
		setAddAndEditBtn(editTodoBtn);
		setEditTodoId(item.id);
		setTodoInputValue(item.text);
	}

	const updateTodo = () => {
		const updatedTodo = {
			id: editTodoId,
			text: todoInputValue
		}
		const updatedTodos = todos.slice();
		const index = todos.findIndex(todo => todo.id === editTodoId);
		updatedTodos[index] = updatedTodo;
		setTodos(updatedTodos);

		// On vide le formulaire aprs mise  jour de la todo,
		// on remet le bouton d'ajout d'une todo et
		// on supprime l'id de la todo  diter.
		setTodoInputValue('');
		setAddAndEditBtn(addTodoBtn);
		setEditTodoId(null);
	}

	const deleteTodo = (item) => {
		const newTodos = todos.filter(todo => todo.id !== item.id);
		notify('Todo deleted.');
		setTodos(newTodos);
	}

	const addTodo = () => {
		let newTodo = {
			id: new Date().valueOf().toString(),
			text: todoInputValue.trim(),
			completed: false
		}
		setTodos([...todos, newTodo]);

		// On vide le formulaire aprs ajout de la nouvelle todo.
		setTodoInputValue('');
	}

	const toggleCompleteTodo = (item) => {
		const newTodos = todos.slice();
		const index = todos.findIndex(todo => todo.id === item.id);
		newTodos[index] = {
			...item,
			completed: !item.completed
		};
		setTodos(newTodos);
	}

	const notify = (message) => {
		const toastOptions = {
			position: "bottom-center",
			autoClose: 5000,
			hideProgressBar: false,
			closeOnClick: true,
			pauseOnHover: true,
			draggable: true,
			progress: undefined,
			theme: "light",
			}
		toast(message, toastOptions);
	}

	// let addBtnBgColor = isValidText(todoInputValue) ? '#4285F4' : 'lightgrey';
	let addBtnBgColor = isValidText(todoInputValue) ? 'black' : 'lightgrey';

	return (
		<div
			style={{backgroundColor: colors[theme].mainBgColor}}
			className="page-main-div"
		>
			<Header />
			<div className="page-content-div" >
				<div id="add-edit-todo-div">
					<input
						className={getClassName('add-todo-input', theme)}
						value={todoInputValue}
						onChange={ev => setTodoInputValue(ev.target.value)}
					/>
					<button
						// style={{backgroundColor: addBtnBgColor}}
						disabled={!isValidText(todoInputValue)}
						className={`fas fa-plus ${getClassName('btn-add', theme)}`}
						onClick={addAndEditBtn.text === 'ADD' ? addTodo : updateTodo}
					></button>
					{/* <TextField
						id="add-edit-todo-textfield"
						variant="outlined"
						value={todoInputValue}
						onChange={ev => setTodoInputValue(ev.target.value)}
						placeholder={todoInputPlaceholder}
					/> */}
					{/* <IconButton
						onClick={addAndEditBtn.text === 'ADD' ? addTodo : updateTodo}
					>
						{addAndEditBtn.text === 'ADD' ?
							<AddCircle sx={{color: '#4285F4'}}/> :
							<Edit sx={{color: '#F4C20D'}}/>
						}
					</IconButton> */}
				</div>
				<div id="todos-list">
					{todos.length !== 0 ?
						<>
							{todos.map(item =>
								<TodoItem
									theme={theme}
									key={item.id}
									item={item}
									handleDelete={_ => deleteTodo(item)}
									handleEdit={_ => editTodo(item)}
									handleToggleComplete={_ => toggleCompleteTodo(item)}
								/>)
							}
						</> :
						<NoTodos text={noTodosText} theme={theme} />
					}
				</div>
			</div>
			<div>
				<ToastContainer
					style={{...toastContainerStyle, color: theme === 'dark' ? '#d3d3d3' : 'black'}}
					position="bottom-center"
					autoClose={5000}
					hideProgressBar={false}
					newestOnTop={false}
					closeOnClick
					rtl={false}
					pauseOnFocusLoss
					draggable
					pauseOnHover
					theme="dark"
					/>
			</div>
		</div>
	);
}

const toastContainerStyle = {
	height: 50,
	width: 150,
	textAlign: 'center',
	fontSize: 17,
	fontFamily: 'Raleway, sans-serif'
}

const NoTodos = ({ text, theme }) => {
	return (
		<div id="no-todos-div">
			<img src={theme === 'dark' ? NoTodosImageDark : NoTodosImageLight} style={{width: 200, height: 200}}/>
			<span id="no-todos-text">
				{text}
			</span>
		</div>
	);
}

const TodoItem = ({ theme, item, handleDelete, handleEdit, handleToggleComplete}) => {
	const itemBgColor = item.completed ?
		{backgroundColor: 'black'} :
		{backgroundColor: 'white'};
	const textStyle = item.completed ?
		{textDecoration: 'line-through', color: 'white'} :
		{textDecoration: 'none', color: 'black'};
	const toggleIcon = item.completed ? faRotateLeft : faCheck;
	const actionBtnColor = item.completed ?
		{color: 'white'} :
		{color: 'black'};

	return (
		<div
			// style={itemBgColor}
			className={`${getClassName('todo-item-div', theme)}`}>
			<span
				style={{textDecoration: item.completed && 'line-through'}}
				className={`${getClassName('todo-item-text', theme)}`}>
				{item.text}
			</span>
			<div className="todo-item-buttons-div">
				<FontAwesomeIcon
					// style={actionBtnColor}
					className="todo-item-update-button"
					icon={faPen}
					onClick={_ => handleEdit()}
				/>
				<FontAwesomeIcon
					// style={actionBtnColor}
					className="todo-item-toggle-complete-button"
					icon={toggleIcon}
					onClick={_ => handleToggleComplete()}
				/>
				<FontAwesomeIcon
					// style={actionBtnColor}
					className="todo-item-delete-button"
					icon={faTrash}
					onClick={_ => handleDelete()}
				/>
			</div>
		</div>
	);
}

TodoItem.propTypes = {
	item: PropTypes.object.isRequired,
	handleDelete: PropTypes.func.isRequired,
	handleEdit: PropTypes.func.isRequired,
	handleToggleComplete: PropTypes.func.isRequired
}

const mapStateToProps = (state) => {
	return {
		activeTheme: state.theme.activeTheme
	}
}

export default connect(mapStateToProps)(Todos);
