import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import './Header.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSun, faMoon } from '@fortawesome/free-solid-svg-icons';
import { NavLink } from 'react-router-dom';

/* Couleurs et thmes */
const colors = {
	light: {
		mainBgColor: '#F0F0F0',
		textColor: '#9b9b9b'
	},
	dark: {
		mainBgColor: '#181818',
		textColor: '#d3d3d3'
	}
}

let activeStyle = {
	textDecoration: "underline",
};

const Header = (props) => {
	const title = props.title;
	const theme = props.activeTheme;
	const dispatch = props.dispatch;

	const toggleTheme = () => {
		const action = {
			type: 'TOGGLE_THEME'
		}
		dispatch(action);
	}

	const getClassName = (baseClassName, theme) => {
		let res = baseClassName;
		res += ` ${baseClassName}-${theme}`;
		return res;
	}

	return (
		<div style={{backgroundColor: colors[theme].mainBgColor}} id="header-main-div">
			<div style={{width: 150}} className="nav-links-div">
				<ul>
					<li>
						<NavLink
							to="/todos"
							className={getClassName('navlink', theme)}
							style={({ isActive }) => isActive ? activeStyle : undefined}
						>
							todos
						</NavLink>
					</li>
					<li>
						<NavLink
							to="/about"
							className={getClassName('navlink', theme)}
							style={({ isActive }) => isActive ? activeStyle : undefined}
						>
							about
						</NavLink>
					</li>
				</ul>
			</div>
			<span style={{...stylesV2.title, color: colors[theme].textColor}}>{title}</span>
			<div style={{width: 150, color: colors[theme].textColor}}>
				<div
					style={{textAlign: 'center'}}
					onClick={toggleTheme}
				>
					<FontAwesomeIcon
						// style={{color: colors[theme].textColor}}
						// style={{color: theme === 'dark' ? '#d3d3d3' : '#4385f4'}}
						style={{color: theme === 'dark' ? '#123473' : '#F4C20D'}}
						icon={theme === 'dark' ? faMoon : faSun}
					/>
					<span className={getClassName('toggle-theme-text', theme)}>
						{` ${theme} mode`}
					</span>
				</div>
			</div>
		</div>
	);
}

const stylesV2 = {
	title: {
		fontSize: 30,
		fontWeight: 700,
		fontFamily: 'Raleway',
		textTransform: 'uppercase'
	}
};

Header.defaultProps = {
	title: "todos"
};

Header.propTypes = {
	title: PropTypes.string
}

const mapStateToProps = (state) => {
	return {
		activeTheme: state.theme.activeTheme
	}
}

export default connect(mapStateToProps)(Header);
