const initialState = {
	activeTheme: 'dark'
}

const toggleTheme = (currentTheme) => {
	if (currentTheme === 'light') return 'dark';
	return 'light';
}

const themeReducer = (state = initialState, action) => {
	let nextState;

	switch(action.type) {
		case 'TOGGLE_THEME':
			nextState = {
				...state,
				activeTheme: toggleTheme(state.activeTheme)
			}
			return nextState || state;
		default:
			return state;
	}
}

export default themeReducer;
